.organizations-header {
  font-size: 40px;
  line-height: 1.1;
  font-family: "Google Sans Regular";
  text-align: center;
  margin-left: 10px;
  margin-right: 10px;
}

.organizations-subtitle {
  font-size: 20px;
  line-height: 1.5;
  font-family: "Google Sans Regular";
  text-align: center;
  margin: 20px auto;
  max-width: 90%;
  padding: 0 20px;
}

.organizations-subheader {
  font-size: 30px;
  line-height: 1.1;
  font-family: "Google Sans Regular";
  text-align: center;
  margin: 20px 10px 15px 10px;
}

#organizations {
  padding-bottom: 0;
  margin-bottom: 0;
}

.organizations-body-div {
  padding-bottom: 0;
  margin-bottom: 0;
}

@media (max-width: 1380px) {
  .organizations-header {
    font-size: 35px;
  }

  .organizations-subtitle {
    font-size: 18px;
  }

  .organizations-subheader {
    font-size: 25px;
  }
}

@media (max-width: 768px) {
  .organizations-header {
    font-size: 25px;
  }

  .organizations-subtitle {
    font-size: 16px;
    line-height: normal;
    margin: 15px auto;
  }

  .organizations-subheader {
    font-size: 20px;
    margin: 15px 10px 10px 10px;
  }
}
